import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import CartBtn from '../buttons/Cart/CartBtn';

export default function Navbar() {
  
  const state =useSelector((state)=> state.addItem)
 

  return (
    <>
    <nav className="navbar navbar-expand-lg navbar-light bg-white py-3 shadow-sm">
  <div className="container">
    <a className="navbar-brand fw-bold fs-4" href="#">POVERIA</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mx-auto mb-2 mb-lg-0">

        <li className="nav-item">
          <Link className="nav-link fw-bolder" to='home'>Home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link fw-bolder" to='collection'>Collection</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link fw-bolder" to='about'>About Us</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link fw-bolder" to='contact'>Contact</Link>
        </li>
       
      </ul>
 <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
    <div className="button">
        <Link to='login' className="btn btn-outline-dark">
           <i className="fa fa-sign-in"></i>  Login</Link>
           <Link to='register' className="btn btn-outline-dark ms-2">
           <i className="fa fa-user-plus"></i> Register</Link>
           <Link to='cart' className='btn btn-outline-dark ms-2' >
           <i className="fa fa-shopping-cart me-1"></i> Cart({state.length})
          </Link>
    </div>

</ul>
     
    </div>
  </div>
</nav>
    
    </>
  )
}
